// Mixins specific to backgrounds of components

@mixin pnp-background-fixed($height: 600px) {
    // Resets the general height 100% on this element
    height: auto;
    max-height: $height;
}

@mixin pnp-background-light {
    background-color: $bg-light;
}

@mixin pnp-background-dark {
    color: $white;
    background-color: $bg-dark;
}

